@import 'mixins/_font';

// ----------  Fonts config  ----------

$font-name: 'Roboto';
$font-path: '../../fonts/Roboto/';


// ----------  Roboto  ----------

@include font-face($font-name, '#{$font-path}RobotoLight',   normal, 300);
@include font-face($font-name, '#{$font-path}RobotoRegular', normal, normal);
@include font-face($font-name, '#{$font-path}RobotoBold',    normal, bold);

// ----------  Custom font  ----------

@import 'utilities/_custom-font';

// ----------  Font icon  ----------

[class*='custom-font-'] {
	display: inline-block;
	line-height: 1;

	&:before {
		display: block; } }
